<script>
import Bus from "../../../utils/Bus";

export default {
  name: "AccountInfoPlatform",
  destroyed() {
    Bus.$off('globalMsgForPlatformUser');
  },
  data() {
    let Cui_realname = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    let LoginId = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    let Password = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    let Phone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (!this.isCellPhone(value)) {
        callback(new Error('!'))
      } else {
        callback()
      }
    };

    return {
      plat_clmShow: false,
      plat_ballShow: false,
      menuLeft: '-180',  //
      menuLeft2: '80',  //
      menuWidth: '80px',//    
      floatDivWidth: 70, //
      floatRightValue: '50px',  //
      floatTopValue: '55px',//
      ballIcon: 'el-icon-more',
      cardBodyHeight: true,
      cardBodyStyle: 'padding-top:10px;padding-bottom:10px;',
      icon1: "el-icon-d-arrow-left",
      icon2: 'el-icon-caret-top',
      tableSize: 19,//
      multipleSelection: [],//table
      tableData: [],//
      delLoginId: [],//
      sortOrderCol: '',

      // 
      realNameByOrg: '',//
      loginIdByOrg: '',//
      userIdByOrg: '',//ID
      checkOrgId: [],//OrgId
      checkAllOrgIds: [],//OrgID
      selType: '',//
      userQueryOrgIds: [],//ID
      treeDataStatus: {},//
      treeData: [],

      //
      tableRowIdMap: new Map(),
      tableCheckedKeys: false,
      permissionType: 'all',
      userQueryRoleIds: {},//ID
      menuNum: 0,//
      tableTreeData: [],//
      tableDataClone: [],

      //
      tableBtnRowIdMap: new Map(),
      tableBtnCheckedKeys: false,
      userQueryBtnIds: {},//ID
      btnNum: 0,//
      treeBtnData: [],
      tableBtnTreeData: [],//
      tableBtnDataClone: [],

      //
      passwdShow: true,//
      tableDialgTitle: '',//
      tableOperFlag: '',//123
      refreshTableData: new Date().getTime(),
      roleSelect: [],//
      roleId: '',//ID
      reLoad: '',//,ID
      submitColm: false,//
      /**  */
      platformUserForm: {
        cui_uuid: '',
        cui_type: '',
        cui_login_id: '',
        cui_password: '123456',
        cui_realname: '',
        cui_concat_phone: '',
        cui_status: true,
        r_memo: ''
      },
      /**  */
      permissionFormRules: {
        cui_login_id: [
          {required: true, validator: LoginId, trigger: 'blur'}
        ],
        cui_realname: [
          {required: true,validator: Cui_realname, trigger: 'blur'}
        ],
        cui_password: [
           {required: true, validator: Password, trigger:'blur'},
           {min: 6,message:"6", trigger:'blur'}
        ],
        cui_concat_phone: [
           {required: true, validator: Phone, trigger:'blur'}
        ]

      },
      /**  */
      platformUserFormVisible: false,
      platformUserOrgVisible: false,
      platformUserBtnVisible: false,
      /**  */
      queryParam: {
        totalRow: 0,
        currentPage: 1,
        pageSize: this.customGlobal.pageSize,
      },
      /**  */
      platUserSearchForm: {
        cui_realname: '',
        cui_login_id: '',
        cui_concat_phone: '',
        cui_statue: '',
        cui_writetime: ''
      },
      /**  */
