        let _jsondata_ = {
          login_id: row.cui_login_id,
          pass_word: this.customGlobal.md5('123456')
        };
        const loading = this.$loading({
          lock: true,
          text: '...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, .7)'
        });
        //4post
        var p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.resetUserPasswd_new,// 
          this.customGlobal.serverUrl.orgBaseURI,//
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
        );
        //post
        p1.then((res) => {
          //  console.log(":"+JSON.stringify(res));
          loading.close();
          if (res.data.status == 'success') {
            this.$message({
              type: 'success',
              message: '!'
            });
          } else {
            //  console.info("");
            loading.close();
            // this.$alert('', '', {
            //   confirmButtonText: '',
            //   callback: action => {
            //     //  console.info('data:'+JSON.stringify(res.data));
            //     if (res.data.msg === '' && res.data.status === 'failed') {
            //       this.$router.replace({path: '/login'});
            //     }
            //   }
            // });
            if (res.data.msg === '' && res.data.status === 'failed') {
              this.$router.replace({path: '/login'});
            }
          }
        }, error => {
          //  console.log(":"+JSON.stringify(error));
          loading.close();
          this.$alert(JSON.stringify(error.message), '!', {
            confirmButtonText: '',
            callback: action => {
            }
          });
        }).catch((res) => {
          //  console.log(":"+JSON.stringify(res));
          loading.close();
          this.$alert(JSON.stringify(res.message), '', {
            confirmButtonText: '',
            callback: action => {

            }
          });
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },


    /**  */
    submitForm(formName) {
      let url_method = '';
      let msg_show = '';
      let flag = this.tableOperFlag;
      switch (flag) {
        case 1:
          url_method = this.customGlobal.serverUrl.platformAccount_add_new;
          msg_show = "";
          break;
        case 2:
          url_method = this.customGlobal.serverUrl.platformAccount_update_new;
          msg_show = "";
          break;
      }
      //RoleIDmap
      var arrRole = new Array();
      let objPermissionId = {};
      let i = 0;
      this.tableRowIdMap.forEach(function (value, key) {
        arrRole[i] = key;
        objPermissionId[key] = true;
        i++;
      });
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let _jsondata_ = {
            cui_uuid: this.platformUserForm.cui_uuid,
            cui_login_id: this.platformUserForm.cui_login_id,
            cui_password: this.customGlobal.md5(this.platformUserForm.cui_password),
            cui_realname: this.platformUserForm.cui_realname,
            cui_concat_phone: this.platformUserForm.cui_concat_phone,
            cui_status: this.platformUserForm.cui_status == true ? '1' : '2',
            plat_org: arrRole,
            permission_id: objPermissionId
          };
          console.log("12234:" + this.platformUserForm.cui_uuid)
          console.log('_jsondata_:' + JSON.stringify(_jsondata_));
          const loading = this.$loading({
            lock: true,
            text: '...',
            target: '.main',
            spinner: 'el-icon-loading',
            background: 'rgba(0,0,0,0.7)'
          });
          //4post
          var p1 = this.customGlobal.postHttpClient(
            url_method,// 
            this.customGlobal.serverUrl.orgBaseURI,//
            {"Content-Type": "application/x-www-form-urlencoded"},
            this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
          );
          //post
          p1.then((res) => {
            //  console.log(":"+JSON.stringify(res));
            this.queryValveCurrentData();
            if (res.data.status == 'success') {
              this.platformUserFormVisible = false;
              // 
              this.tableRowIdMap = new Map();
              this.noCheckTreeTable(this.tableTreeData);
              this.queryValveCurrentData();
              loading.close();
              this.$message({
                type: 'success',
                message: msg_show + '!'
              });
            } else {
              //  console.info("");
              loading.close();
              // this.$alert('',   '', {
              //   confirmButtonText: '',
              //   callback: action => {
              //     //  console.info('data:'+JSON.stringify(res.data));
              //     if (res.data.msg === '' && res.data.status === 'failed') {
              //       this.$router.replace({path: '/login'});
              //     }
              //   }
              // });
              if (res.data.msg === '' && res.data.status === 'failed') {
                this.$router.replace({path: '/login'});
              }
            }
          }, error => {
            loading.close();
            //  console.log(":"+JSON.stringify(error));
          }).catch((res) => {
            loading.close();
            //  console.log(":"+JSON.stringify(res));
          });

        } else {
          //  console.log('error submit!!');
          return false;
        }
      });
    },


  },

}

</script>

